import axios from 'axios'
import axiosService from 'axios-service'


const setTicketToHeader = config => {
  const { headers } = config;
  console.log(headers)
};

const setTicketToParams = config => {
  const { params = {} } = config;
  config.params = {
    ...params,
  }
};

// const TIME_OUT = 5000
// const TIME_OUT = 3e+4;
const TIME_OUT = 10 * 60 * 1000

axiosService.init(axios, {
  requestDefaults: {
    // , 
    autoLoading: true,
    // response.data
    // servermsg(
    msgKey: 'msg',
    // serverkey
    dataKey: 'data',
    // serverkey
    codeKey: 'status',
    // server, : response.data, http(200)
    successCode: 0,
  }
});

// 
axios.defaults.timeout = TIME_OUT;
// withCredentials
axios.defaults.withCredentials = true;

// 
axios.interceptors.request.use(config => {
  if (config.autoLoading === undefined || config.autoLoading === true) {
    // loading.show();
  }

  //  ticket  header  query 
  setTicketToHeader(config)
  setTicketToParams(config)
  return config
}, error => {
  // loading.hide();
  console.error('');
  return Promise.reject(error)
});

// 
axios.interceptors.response.use(data => {
  const { autoLoading } = data.config;
  console.log(autoLoading)
  return data
}, error => {
  // loading.hide();
  return Promise.reject(error)
});
